const {
  IonApp,
  IonPage,
  IonContent,
  IonList,
  IonItem,
  IonLabel,
  IonHeader,
  IonTitle,
  IonToolbar,
  IonCheckbox,
} = require("@ionic/react");

export default function App() {
  const todos = [
    { text: "ミルクを買う", finished: false },
    { text: "卵を買う", finished: false },
  ];
  return (
    <IonApp>
      <IonPage>
        <IonHeader>
          <IonToolbar>
            <IonTitle>TODO App</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            {todos.map((item, i) => {
              return (
                <IonItem key={i}>
                  <IonLabel>{item.text}</IonLabel>
                  <IonCheckbox value={item.finished} />
                </IonItem>
              );
            })}
          </IonList>
        </IonContent>
      </IonPage>
    </IonApp>
  );
}
